	async function formataOpcoesCheckBox(arr, tipoOpcao) {
		const filtroCheckbox = arr.filter((obj) => obj.TpCampo === `${tipoOpcao}`);
		const objetosFiltradosAux = {};
		// eslint-disable-next-line no-plusplus
		for (let i = filtroCheckbox.length - 1; i >= 0; i--) {
			const obj = filtroCheckbox[i];
			if (!objetosFiltradosAux[obj.CodResposta]) {
				objetosFiltradosAux[obj.CodResposta] = obj;
			}
		}
		return Object.values(objetosFiltradosAux);
	}

